.about-me-section{
  opacity:0;
  animation:fadeIn 1000ms linear 0s forwards;
}

.about-me-section > div{
  margin-bottom:32px;
}

.about-me-section > div:last-child{
  margin-bottom:0;
}

.about-me-section > div:nth-child(1){
  display:flex;
  flex-direction:row-reverse;
  justify-content:space-between;
  align-items:center;
  gap:64px;
}

.about-me-section > div:nth-child(1) > p{
  margin:0;
  width:50%;
  height:calc(8px * 26);
  line-height:calc(16px * 2);
}

.about-me-section > div:nth-child(1) > p a{
  text-decoration:none;
  border-bottom:1px solid var(--c_3);
  color:var(--c_2);
}

.skills-box{
  margin-top:32px;
  display:flex;
  flex-direction:row;
  justify-content:flex-start;
  align-items:center;
  flex-wrap:wrap;
  gap:32px;
  padding:0;
}

.skills-box > li{
  --percent:0%;
  flex-shrink:0;
  display:block;
  width:256px;
  line-height:48px;
  padding:0 24px;
  border:1px solid var(--c_4);
  background-image:linear-gradient(to right, var(--c_4) var(--percent), transparent 0%);
  box-sizing:border-box;
}

.skills-box > li.html{
  --percent:70%;
}

.skills-box > li.css{
  --percent:80%;
}

.skills-box > li.js{
  --percent:70%;
}

.skills-box > li.jquery{
  --percent:80%;
}

.skills-box > li.reactjs{
  --percent:10%;
}

.certificates-box{
  margin:32px 0 0;
  padding-left:24px;
}

.certificates-box > li{
  line-height:calc(16px * 2);
}

.certificates-box > li > a{
  text-decoration:none;
  border-bottom:1px solid var(--c_3);
  color:var(--c_2);
  font-variant-ligatures: common-ligatures;
}



@media screen and (max-width:920px) and (orientation:portrait){

  .about-me-section > div:nth-child(1) > p{
    height:calc(8px * 32);
  }

}

@media screen and (max-width:480px) and (orientation:portrait){

  .about-me-section > div:nth-child(1){
    flex-direction:column;
    align-items:flex-start;
    gap:16px;
  }

  .about-me-section > div:nth-child(1) > p{
    width:auto;
    height:auto;
  }

  .skills-box{
    gap:16px;
  }

  .skills-box > li{
    width:100%;
    line-height:32px;
    padding:0 16px;
  }

}