.nav-bar{
  height:64px;
  width:auto;
  padding:8px 64px;
  background-color:var(--c_1);
  border-bottom:2px solid var(--c_4);
  box-sizing:border-box;
  position:fixed;
  top:0;
  right:0;
  z-index:10;

  animation:fadeInRight 500ms linear 0s;
}

.nav-bar > ul{
  margin:0;
  padding:0;
  height:100%;
  display:inline-flex;
  flex-direction:row;
  justify-content:center;
  align-items:center;
  gap:16px;
  box-sizing:border-box;
}

.nav-bar > ul > li{
  display:inline-block;

  opacity:0;
  transform:translate3d(0,-100%,0);
  animation-name:fadeInDown;
  animation-duration: 500ms;
  animation-timing-function:linear;
  animation-fill-mode:forwards;
}

.nav-bar > ul > li:nth-child(1){
  animation-delay:500ms;
}

.nav-bar > ul > li:nth-child(2){
  animation-delay:750ms;
}

.nav-bar > ul > li:nth-child(3){
  animation-delay:1000ms;
}

.nav-bar > ul > li > a{
  text-decoration:none;
  color:var(--c_2);
  padding:4px 8px;
  transition:font-weight, color 350ms ease-in-out;
}

.nav-bar > ul > li > a.active{
  font-weight:bold;
  color:var(--c_3);
}

.nav-bar > button{
  display:none;
  width:48px;
  height:48px;
  font-size:0;
  background-color:var(--c_1);
  border:0;
  padding:0;
  box-sizing:border-box;
}


@media screen and (max-width:920px) and (orientation:portrait){

  .nav-bar{
    padding:8px 32px;
  }

}


@media screen and (max-width:480px) and (orientation:portrait){

  .nav-bar{
    padding:8px 16px;
    border-bottom:none;
  }

  .nav-bar > ul{
    width:100%;
    height:100%;
    flex-direction:column;
    align-items:flex-start;
    background-color:var(--c_1);
    padding:64px 32px;
    position:fixed;
    top:0;
    right:-100%;
    z-index:-1;
    transition:right 350ms ease-in-out;
  }

  .nav-bar > ul.show{
    right:0;
  }

  .nav-bar > ul > li{
    animation-play-state: paused;
    animation-name:none;
  }

  .nav-bar > ul.show > li{
    animation-name:fadeInDown;
    animation-play-state: running;
  }

  .nav-bar > ul > li > a{
    font-size:32px;
  }

  .nav-bar > button{
    display:inline-block;
  }

}