.preloader{
  width:100vw;
  height:100vh;
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  background-color:var(--c_1);
  box-sizing:border-box;
  position:fixed;
  top:0;
  left:0;
  z-index:100;
}

.preloader ~ *{
  display:none !important;
}

.loader{
  display:block;
  height:24px;
  width:180px;
  background-color:var(--c_4);
  box-sizing:border-box;
}

.loader.blink{
  opacity:0;
  animation: blinking 800ms linear 0s infinite;
}

.loader.fill{
  position: relative;
}

.loader.fill::after{
  content:"";
  width:0%;
  height:100%;
  background-color:var(--c_3);
  animation:filling 2s linear 0s forwards;
  position:absolute;
  top:0;
  left:0;
}

@keyframes blinking{
  0%{
    opacity:0;
  }
  100%{
    opacity:1;
  }
}

@keyframes filling{
  0%{
    width:0%;
  }
  100%{
    width:100%;
  }
}
