.footer{
  height:64px;
  padding:8px 64px;
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:flex-end;
  gap:8px;
  box-sizing:border-box;
}

.footer > .copyright-box{
  font-size:var(--fs_2);

  opacity:0;
  animation:fadeIn 500ms linear 0s forwards;
}

.footer > .contact-me-box{
  height:24px;
  display:flex;
  flex-direction:row;
  justify-content:center;
  align-items:center;
  gap:16px;
  transform-origin:0% 50%;
  transform:rotate(-90deg);
  position: fixed;
  left:64px;
  bottom:4px;

  opacity:0;
  animation:fadeIn 500ms linear 0.5s forwards;
}

.footer > .contact-me-box > span{
  margin-right:48px;
  background-color:var(--c_1);
  font-size:var(--fs_2);
  padding:0 4px;
  position:relative;
}

.footer > .contact-me-box > span:after{
  content:"";
  height:2px;
  width:132px;
  background-color:var(--c_4);
  position:absolute;
  top:7px;
  left:-16px;
  z-index:-1;
}

.footer > .contact-me-box > a{
  text-decoration:none;
  cursor:pointer;
  font-size:0;
  transform-origin:50%;
  transform:rotate(90deg);
}

.footer > .contact-me-box > a > span.remixicon{
  font-size:0;
}


@media screen and (max-width:920px) and (orientation:portrait){

  .footer{
    padding:8px 32px;
  }

  .footer > .contact-me-box{
    left:32px;
  }

}


@media screen and (max-width:480px) and (orientation:portrait){

  .footer{
    align-items:center;
    padding:8px 16px;
  }

  .footer > .contact-me-box{
    flex-direction:row-reverse;
    position:static;
    transform: none;
  }

  .footer > .contact-me-box > span{
    display:none;
  }

  .footer > .contact-me-box > a{
    transform:rotate(0deg);
  }

}