.projects-section{
  opacity:0;
  animation:fadeIn 1000ms linear 0s forwards;
}

.projects-section > div{
  margin-bottom:32px;
}

.projects-section > div:last-child{
  margin-bottom:0;
}

.project-card{
  margin-top:32px;
  display:flex;
  flex-direction:row;
  justify-content:flex-start;
  align-items:center;
  flex-wrap: wrap;
  gap: 16px 32px;
  padding:16px;
  border:1px solid var(--c_4);
  box-sizing:border-box;
  transition:border-color 350ms linear;
}

.project-card:nth-child(odd){
  flex-direction:row-reverse;
}

.project-card:hover{
  border-color:var(--c_3);
}

.project-card > div{
  flex-shrink:0;
}

.project-card > div:nth-child(1){
  align-self:flex-start;
  width:calc(100% - 32px - 320px);
}

.project-card > div:nth-child(1) > p{
  margin:16px 0 0;
  line-height:calc(16px * 2);
}

.project-card > div:nth-child(2){
  --width:320px;
  width:var(--width);
  height:calc(var(--width) / 1.6);
  background-color:var(--c_5);
  background-repeat:no-repeat;
  background-size:100%;
  background-position:center;
  background-blend-mode:multiply;
  transition:background 350ms linear;
}

.project-card > div:nth-child(2):hover{
  background-color:var(--c_6);
  background-size:140%;
}

.project-card > span{
  margin-left:auto;
  font-size:var(--fs_2);
}

.project-card > span > a{
  text-decoration:none;
  border-bottom:1px solid var(--c_3);
  color:var(--c_2);
}


@media screen and (max-width:920px) and (orientation:portrait){

  .project-card > div:nth-child(1){
    width:calc(100% - 32px - 256px);
  }

  .project-card > div:nth-child(2){
    --width:256px;
  }

}

@media screen and (max-width:480px) and (orientation:portrait){

  .project-card{
    flex-direction:column-reverse;
    gap:16px;
  }

  .project-card:nth-child(odd){
    flex-direction:column-reverse;
  }

  .project-card > div:nth-child(1){
    width:100%;
  }

  .project-card > div:nth-child(2){
    width:100%;
    height:calc(8px * 22);
  }

  .project-card > span{
    order:-1;
  }

}