.home-section{
  width:100%;
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:flex-start;
  gap:32px;
  background-image:var(--img-1);
  background-repeat:no-repeat;
  background-size:560px;
  background-position:right 100%;

  opacity:0;
  animation:fadeIn 1000ms linear 0s forwards;
}

.tag-line{
  margin:0;
}

.tag-line > span{
  display:block;
}

.tag-line > span:nth-child(1){
  margin-bottom:8px;
  font-size:24px;
}

.tag-line > span:nth-child(2),
.tag-line > span:nth-child(3){
  font-size:64px;
}

.tag-line > span:nth-child(2){
  width:fit-content;
  padding:0 8px;
  background-color:var(--c_3);
  color:var(--c_1);
}

.tag-line > span:nth-child(3) > span{
  font-weight:bold;
  color:var(--c_3);
  position: relative;
}

.tag-line > span:nth-child(3) > span::after{
  content:"";
  width:24px;
  height:24px;
  background-color:var(--c_2);
  box-sizing:border-box;
  position:absolute;
  bottom:-16px;
  right:-16px;
}

.typing-box{
  margin:0;
}

.typing-box .typed-cursor{
  font-weight:bold;
  color:var(--c_3);
}


@media screen and (max-width:920px) and (orientation:portrait){

  .home-section{
    background-size:320px;
  }

}


@media screen and (max-width:480px) and (orientation:portrait){
  
  .home-section{
    background-size:240px;
  }

  .tag-line > span:nth-child(1){
    font-size:18px;
  }

  .tag-line > span:nth-child(2),
  .tag-line > span:nth-child(3){
    font-size:48px;
  }

  .tag-line > span:nth-child(3) > span::after{
    width:16px;
    height:16px;
    bottom:-12px;
    right:-12px;
  }

}