:root{

  --ff_p: "Space Mono", monospace;

  --fs_1: 8px;
  --fs_2: 10px;
  --fs_3: 12px;
  --fs_4: 14px;
  --fs_5: 16px;
  --fs_6: 18px;
  --fs_7: 20px;
  --fs_8: 22px;
  --fs_9: 24px;

  --c_1: hsl(0 0% 99%);
  --c_2: hsl(0 0% 40%);
  --c_3: hsl(0 0% 12%);
  --c_4: hsl(0 0% 90%);
  --c_5: hsl(0 0% 50% / 5%);
  --c_6: hsl(0 0% 90% / 1%);

  --img-1: url('./imgs/img-1.svg');
  
}

html{
  -webkit-tap-highlight-color:transparent;
}

html::-webkit-scrollbar{
  width:8px;
  height:8px;
  background-color:var(--c_1);
  box-sizing:border-box;
}

html::-webkit-scrollbar-thumb{
  width:8px;
  border:2px solid var(--c_1);
  background-color:var(--c_4);
  box-sizing:border-box;
}

body{
  margin:0;
  font-family:var(--ff_p);
  font-size:var(--fs_5);
  background-color:var(--c_1);
  color:var(--c_2);
  font-variant-ligatures:no-common-ligatures;
}

a:is(:hover, :focus),
button:is(:hover, :focus){
  outline:1px dashed var(--c_3);
}

main > section{
  min-height:calc(100vh - 64px);
  padding:128px 128px 64px;
  box-sizing:border-box;
}

h1.h1{
  margin:0;
  font-size:64px;
  font-weight:normal;
  line-height:calc(64px * 1);
  color:var(--c_3);
  position:relative;
}

h1.h1::before{
  content:"";
  width:64px;
  height:64px;
  background-color:var(--c_4);
  position:absolute;
  top:-16px;
  left:-16px;
  z-index:-1;
}

h2.h2{
  margin:0;
  font-size:24px;
  font-weight:normal;
  color:var(--c_3);
  position:relative;
}

h2.h2::before{
  content:"";
  width:64px;
  height:4px;
  background-color:var(--c_4);
  position:absolute;
  bottom:-4px;
}


[class*="remixicon"]:is(span, i){
  --size:20;
  --color:var(--c_2);
}





@media screen and (max-width:920px) and (orientation:portrait){

  main > section{
    padding:128px 64px 64px;
  }

}


@media screen and (max-width:480px) and (orientation:portrait){
  
  main > section{
    padding:128px 32px 64px;
  }

  h1.h1{
    font-size:48px;
  }

  h1.h1::before{
    top:-8px;
  }

  h1.h1::before{
    width:48px;
    height:48px;
  }

  h2.h2{
    font-size:20px;
  }

}


@media (prefers-color-scheme:dark){

  :root{

    --c_1:hsl(80 20% 2%);
    --c_2:hsl(80 10% 60%);
    --c_3:hsl(80 70% 40%);
    --c_4:hsl(80 20% 8%);
    --c_5: hsl(80 70% 50% / 50%);
    --c_6: hsl(80 70% 50% / 1%);

    --img-1: url('./imgs/img-2.svg');

  }

}